<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="bloquera.meta_semana"
    :progress="bloquera.progress"
    :name="bloquera.nombre"
    :depto="bloquera.depto"
    :days="bloquera.dias_laborados"
    :m3="bloquera.m3_persona"
    :bonus_p="bloquera.bono_persona"
    :bonus_tot="bloquera.bono_depto"
    :asis="bloquera.asistencia"
    :extra="bloquera.$_extra_m3"
    :bonus_prod="bloquera.bono_productividad"
    :bonus_goals="bloquera.bono_metas"
    />
</div>
</template>

<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Moldeo',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'bloquera',
            userCode: this.$route.params.user,
            city: 'monterrey'
        }); 
    },
    computed: {
        isLoading() {            
            return this.$store.getters["plants/loading"];
        },
        bloquera() {            
            return this.$store.getters["plants/data"];
        }
    }
}
</script>